<template>
  <div id="about">
    <common-header title="关于我们"></common-header>
    <div class="about__logo">
      <img src="../../../assets/about_logo.png" alt="">
      <div class="logo__info">版本号: 6.0.3</div>
    </div>
    <div class="about__link">
      <Cell title="关于我们" @click="linkToUrl({name:'AboutUs',query:{id:aboutUsId,title:'关于我们'}})"/>
      <Cell title="用户协议" @click="linkToUrl({name:'AboutUs',query:{id:userId,title:'用户协议'}})"/>
      <Cell title="玩法" @click="linkToUrl({name:'AboutUs',query:{id:gameId,title:'玩法介绍'}})"/>
      <Cell title="联络我们" @click="linkToUrl({name:'AboutUs',query:{id:contactId,title:'联络我们'}})"/>
      <!--<Cell title="用户协议" @click="linkTo('User')"/>
      <Cell title="玩法" @click="linkTo('Game')"/>
      <Cell title="联络我们" @click="linkTo('Contact')"/>-->
    </div>

    <div class="about__copy">
      <p>@Copyright 2016-2017 youdeman ALL Rights</p>
      <p>广州何人谈网络信息开发有限公司 版权所有</p>
    </div>
  </div>
</template>

<script>
  import Cell from '@/components/Cell/Cell';

  export default {
  	data(){
  		return{
  			aboutUsId:'',
  			userId:'',
  			gameId:'',
  			contactId:'',
  			
  		}
  	},
  	created(){
  		this.getQueryPublicSettings()
  	},
  	
    components: {
      Cell
    },
    methods:{
    	getQueryPublicSettings(){
    		this.$Api.getQueryPublicSettings().then((res)=>{
    			console.log(res)
    			if(res.q.s == 0){
    				let items = res.q.items
    				items.forEach((item)=>{
    					if(item.key == '关于我们'){
    						this.aboutUsId = item.value
    					}
    					if(item.key == '用户协议'){
    						this.userId = item.value
    					}
    					if(item.key == '玩法介绍'){
    						this.gameId = item.value
    					}
    					if(item.key == '联络我们'){
    						this.contactId = item.value
    					}
    				})
    			}
    		})
    	},
    }
  }
</script>

<style lang="scss">
  #about {
    position: relative;
    height: 100%;
    background: #f0f0f0;

    .about__logo {
      padding: 1.06rem 0 ;
      text-align: center;
      img {
        width: 2.67rem;
        height: 2.67rem;
      }

      .logo__info {
        padding: 0.2rem 0;
        font-size: 0.427rem;
        color: #de7fef;
      }
    }

    .about__copy {
      position: absolute;
      bottom: 0.2rem;
      width: 100%;
      color: #999;
      font-size: 0.3467rem;
      text-align: center;
    }
  }
</style>



